.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  padding: 20px;
  z-index: 999;
  height: 50px;
  background-color: #181838;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.link {
  text-decoration: none;
  color: #fff;
  margin-right: 10px;
}

.link:last-child {
  margin-right: 0;
}

.activeLink {
  color: tomato;
}

.userMenu {
  display: flex;
  align-items: center;
}

.headerBtn {
  margin: 0;
  padding: 5px 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.15;
  letter-spacing: 0.03em;
}
